<template>
  <div>
      <div class="title">
          员工管理
      </div>
      <!-- 这是关联用户 -->
      <div class="relationBox_b">
          <div class="jgTab">
            <!-- 这是绑定的人员数据表 -->
            <div class="tablebox">
              <div class="jieGou_tit">
                员工
                <div class="jieGou_tit_rigth">
                  <el-button size="small" @click="Fnunactivestafflist">查看申请用户</el-button>
                  <el-button type="primary" size="small" @click="AdialogFormVisible = true">新增用户</el-button>
                  <!-- 有提示信息 -->
                  <!-- <el-badge :value="2" class="item" type="warning">
                    <el-button size="small">回复</el-button>
                  </el-badge> -->
                </div>
                <el-divider></el-divider>
              </div>
              <!-- <div>
                <button @click="Fncompanystaff">公司成员表获取</button>
                <button @click="Fnunactivestafflist">查看新员工申请表</button>
                <button @click="Fnenrollstaff">同意添加新员工加入申请</button>
                <button @click="Fndeactivatestaff">停用员工</button>
              </div> -->
              <el-table
                :data="tableDatareRE"
                stripe
                style="width: 100%">
                <el-table-column
                  prop="username"
                  label="用户名">
                </el-table-column>
                <el-table-column
                  prop="phone_numbers"
                  label="手机">
                </el-table-column>
                <el-table-column
                  label="操作"
                  width="180">
                  <template slot-scope="scope">
                      <!-- scope.row.name -->
                      <el-button @click.native="Fndeactivatestaff(scope.row.user_id)" size="small" type="danger">停用</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
      </div>
      <!-- 申请加入的用户-->
      <el-dialog title="申请列表" :visible.sync="dialogFormVisible">
        <!-- 员工申请列表 -->
        <el-table
          :data="ADDtableData"
          border
          style="width: 100%">
          <el-table-column
            prop="username"
            label="用户名"
            width="180">
          </el-table-column>
          <el-table-column
            prop="phone_numbers"
            label="手机号">
          </el-table-column>
          <el-table-column
            label="操作"
            width="120">
            <template slot-scope="scope">
              <el-button type="success" @click.native="Fnenrollstaff(scope.row.user_id)">同意加入</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-dialog>
      <!-- 新增员工 -->
      <el-dialog title="新增员工" :visible.sync="AdialogFormVisible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="用户名" prop="staff_name">
            <el-input v-model="ruleForm.staff_name"></el-input>
          </el-form-item>
          <el-form-item label="手机号" prop="staff_phone">
            <el-input v-model="ruleForm.staff_phone"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="staff_password">
            <el-input v-model="ruleForm.staff_password"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">新增</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from 'vuex'
export default {
    data(){
        return{
            tableDatareRE:[],//公司成员列表
            dialogFormVisible: false,//表单打开新增用户
            ADDtableData: [],//员工申请加入列表
            tableDatareREget:[],//获取公司成员表
            AdialogFormVisible:false,//新增员工
            ruleForm: {//新增用户提交表单
              staff_name: "",
              staff_phone: "",
              staff_password: ""
            },
            rules: {//提交表单验证
              staff_name: [
                { required: true, message: '请输入活动名称', trigger: 'blur' },
                { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
              ],
              staff_phone: [
                { required: true, pattern:/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,message: '手机号未填或格式不正确', trigger: 'blur' }
              ],
              staff_password: [
                { required: true, pattern:/^(\w){6,20}$/,message: '请输入6-20个字母、数字、下划线', trigger: 'blur' }
              ],
            }
        };
    },
    methods:{
      submitForm(formName) {//新增员工表单提交
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.AdialogFormVisible=false
            this.ruleForm.user_id=this.userInfo.user_id
            url.managercreatestaff(this.ruleForm)
            .then(res=>{
              if(result.data.code[0]==200){
                this.$message.success(result.data.message[0]);
                this.Fncompanystaff();//调取查看公司员工
              }else{
                this.$message.console.error(result.data.message[0]);
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      Fncompanystaff(){//调取查看公司员工
        url.companystaff({
          "company_id": this.userInfo.u_company_id,
          "user_id": this.userInfo.user_id
        }).then(result=>{
          console.log(result,'//调取查看公司员工')
          if(result.data.code[0]==200){
            let xx=result.data.data
            let newlist=[]
            xx.forEach(element => {
            element.fields.user_id=element.pk
            newlist.push(element.fields)
            });
            this.tableDatareRE=newlist
          }
        })
      },
      Fnunactivestafflist(){//查看新员工申请表
        this.dialogFormVisible = true
        url.unactivestafflist({
          "company_id": this.userInfo.u_company_id,
          "user_id": this.userInfo.user_id
        }).then(result=>{
          console.log(result,'--------查看新员工申请表')
          let xx=result.data.data
          let newlist=[]
          xx.forEach(element => {
          element.fields.user_id=element.pk
          newlist.push(element.fields)
          });
          this.ADDtableData=newlist

        })
      },
      Fnenrollstaff(user_id){//同意添加新员工加入申请

        // "user_id": 29,同意的员工id
        // "user_id1": 28公司管理员id
        url.enrollstaff({
          user_id: user_id,
          user_id1: this.userInfo.user_id,
          company_id:this.userInfo.u_company_id,
        }).then(res=>{
          // 同意添加新员工加入申请
          this.Fnunactivestafflist()
          //调取查看公司员工
          this.Fncompanystaff()
        })
      },
      Fndeactivatestaff(id){//停用员工
        url.deactivatestaff({
          user_id:id,
          company_id:this.userInfo.u_company_id,
          user_id1: this.userInfo.user_id
        }).then(res=>{
          this.$message.success(res.data.message[0]);
          this.Fncompanystaff()//调取查看公司员工
        })
      },
    },
    computed:{
      ... mapState(['userInfo'])
    },
    mounted(){
      this.Fncompanystaff();//调取查看公司员工
    }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.relationBox_b
  padding 30px
  .jgTab
    display flex
    .jieGou
      width 300px
    .jieGou_tit
      margin-bottom 17px
      .jieGou_tit_rigth
        width 220px
        float right
    .tablebox
      flex 1
      margin-left 30px
</style>


